@charset "utf-8";
* {
  margin: 0;
  padding: 0;
}
h1 {
  padding-bottom: 20px;
}
html,
body {
  display: table;
  width: 100%;
  height: 100%;
}
body {
  display: table-cell;
  vertical-align: middle;
}
.seafield {
  width: 260px;
  height: 259px;
  border: 1px solid #808080;
  border-right-width: 0px;
  margin: 0 50px 0 10px;
  overflow: hidden;
  float: left;
}
.seafield span {
  border-right: 1px solid #888;
  border-bottom: 1px solid #888;
  width: 25px;
  height: 25px;
  cursor: pointer;
  float: left;
}
.seafield span.seagrid {
  background-color: #0cf;
}
.seafield span.sheep {
  background-color: #008000 !important;
}
.seafield span.killed,
.seafield span.busy {
  background-color: #f00 !important;
  cursor: auto;
}
.seafield span.disabled {
  cursor: auto;
  background-color: #777 !important;
}
.gamedata {
  height: 500px;
  width: 400px;
  border-left: 1px solid #eee;
  overflow-y: hidden;
}
#countShoot {
  height: 100%;
  overflow-y: auto;
  padding-left: 10px;
}
#reloadGame {
  display: none;
  padding: 10px 20px;
  background-color: #eee;
  color: #000;
  font-weight: bold;
  border: 1px solid #000;
  margin-left: 40px;
  cursor: pointer;
  outline-width: 0px;
}
